<template>
  <div>
    <z-paging
      ref="pagingRef"
      v-model="dataList"
      @query="queryList"
      empty-view-text="暂无动态"
      empty-view-img="/static/images/empty.png"
      :empty-view-img-style="{ width: '160px', height: '160px' }"
    >
      <ActivityItem
        v-for="item in dataList"
        :key="item"
        :item="item"
        @update-voluntary="getUpdate"
      />
    </z-paging>
  </div>
</template>

<script>
import ActivityItem from '@/components/ActivityItem/index.vue'
export default {
  components: {
    ActivityItem,
  },
  data() {
    return {
      dataList: [],
    }
  },
  methods: {
    getUpdate(item) {
      // this.queryList(1, 10);
    },
    async queryList(pageNo, pageSize) {
      console.log('[ pageNo ] >', pageNo)
      console.log('[ pageSize ] >', pageSize)
      let params = {
        page: pageNo,
        size: pageSize,
        member_id: this.$store.user.userInfo.member_id,
      }
      let res = await this.$api.getVoluntaryActivityMyLove(params)
      if (res.code == 200) {
        let data = res.data.list
        let dealData = data.map((item) => {
          let formatObj = {
            addressFormat: item.province_str + item.county_str + item.city_str + item.address,
            startTime: this.$dayjs(item.c_time * 1000).format('YYYY-MM-DD HH:mm:ss'),
          }
          return {
            ...item,
            ...formatObj,
          }
        })
        this.dataList = dealData
        this.$refs.pagingRef?.complete(this.dataList)
      }
    },
    change1() {},
  },
}
</script>

